<script context="module">
  import moment from 'moment';
  import { _ } from '../services/il8n';

  const getDates = (schedule) => {
    const allDates = schedule.map((s) => moment(s.scheduled_start).format('DD.MM'));

    return allDates.filter((val, index, arr) => arr.indexOf(val) === index);
  };

  //const getMatchesByDate = (date, schedule) => schedule.filter((m) => moment(m.scheduled_start).format('DD.MM') === date);
</script>
<script>
  import { getUniqueDates, getScheduleForDate } from '../utils/scheduleParser.js';
  import HorizontalNavigation from '../HorizontalNavigation/HorizontalNavigation.svelte';
  import MatchCard from '../MatchCard/MatchCard.svelte';

  export let standings = {};

  let uniqueDates = getUniqueDates(standings.tournament);
  let selectedDate = uniqueDates[0];

  let matches = getScheduleForDate(selectedDate, standings);

  $ : {
    uniqueDates = getUniqueDates(standings.tournament);
    matches = getScheduleForDate(selectedDate, standings);
  }

  const navigationClick = (val) => {
    selectedDate = val;
    matches = getScheduleForDate(selectedDate, standings);
  };
</script>

<div>
  <div class="text-xs">
    <HorizontalNavigation
      navigationItems={uniqueDates.map((d) => ({
        label: moment(d).format('DD.MM'),
        value: d,
        active: selectedDate === d ? true : false,
      }))}
      onClick={navigationClick}
    />
  </div>

  <div class="text-white  text-center  my-4  text-xs">{$_('schedule.timezones')}</div>

  <div class="flex  flex-col  text-sm">
    {#each matches as match, i}
      <div class="{i === 0 ? 'mb-2' : 'my-2'}">
        <MatchCard {match} />
      </div>
    {/each}
  </div>
</div>
