import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';

export function tooltip(node, props) {
  if (props === null) return null;
  
  const instance = tippy(node, props);

  return {
    update(props) {
      instance.setContent(props.content);
    },
    destroy() {
	     instance.destroy();
		}
	}
}
