<style>
    .match-bracket-container
        .match-bracket-parents
        > .match-bracket-row.par-1
        > .node-connector {
        display: none;
    }
    .match-bracket-container > .match-label {
        width: 136px;
        top: -13px;
    }
    .match-bracket-container > .connector {
        height: calc(50% - 45px);
        right: 62px;
        top: 9px;
    }


</style>
<script>
    import MatchBracket from "./../MatchBracket/MatchBracket.svelte";
    import { getContext } from 'svelte';
    import { getTheme } from '../../utils/theme.js';

    const state = getContext('state');
    const theme = getTheme($state);

    export let match = {};
    export let depth = 0;
    export let bracket;

    const isParentVisible = parent => parent && parent.depth <= depth - 1;
    const visibleParentsAmount = match.parents.filter(isParentVisible).length;
</script>

{#if match.depth > depth}
    <div />
{:else if match.depth !== depth}
    <svelte:self {match} depth={depth - 1} bracket={bracket} />
    <MatchBracket teams={[]} bracket={bracket} />
{:else if !match.parents || !match.parents.length}
    <div class="match-bracket-container  relative  flex  items-center  justify-end">
        <MatchBracket
          first={true}
          teams={match.teams}
          let:teams={teams}
          bracket={bracket}
          startTime={match.scheduled_start}
        />
        {#if match.label}
            <div class="match-label  text-white  text-center  absolute  right-0  text-right  mx-6px  mt-1">
                {match.label}
            </div>
            <div class="connector  absolute  w-0  border-gray-500  border-r  border-dashed" />
        {/if}
    </div>
{:else}
    <div class="match-bracket-container  relative  flex  items-center  justify-end">
        <div class="match-bracket-parents  relative">
            {#if isParentVisible(match.parents[0])}
                <div class="match-bracket-row  flex  relative  justify-end  par-{visibleParentsAmount}">
                    <svelte:self depth={depth - 1} match={match.parents[0]} bracket={bracket} />
                    <div class="node-connector  h-1/2 top-1/2  absolute  w-px  bg-{theme}primary-2 right-0" />
                </div>
            {/if}
            {#if isParentVisible(match.parents[1])}
                <div class="match-bracket-row  flex  relative  justify-end  par-{visibleParentsAmount}">
                    <svelte:self depth={depth - 1} match={match.parents[1]} bracket={bracket} />
                    <div class="node-connector  {!isParentVisible(match.parents[0]) ? 'top-1/2':''}  h-1/2  absolute  w-px  bg-{theme}primary-2  right-0" />
                </div>
            {/if}
        </div>
        <MatchBracket
          teams={match.teams}
          let:teams={teams}
          bracket={bracket}
          startTime={match.scheduled_start}
        />
        {#if match.label}
            <div class="match-label  text-white  text-center  absolute  mt-1">
                {match.label}
            </div>
            <div class="connector  absolute  w-0  border-gray-500  border-r  border-dashed" />
        {/if}
    </div>
{/if}
