<script>
    import { getContext, onDestroy } from 'svelte';
    import ScoreMatrixColumn from './ScoreMatrixColumn/ScoreMatrixColumn.svelte';

    export let scores = [];

    const state = getContext('state');
    let debounceTimeout;
    let debounce = false;

    const onScroll = () => {
      if (!debounce) {
        if ($state && $state.analyticsUid) ga('send', 'event', 'Scroll', `${$state.analyticsUid}:${new Date().getTime()}`, 'Score Matrix');
        debounce = true;

        debounceTimeout = setTimeout(() => debounce = false, 1000);
      }
    };

    onDestroy(() => {
      clearTimeout(debounceTimeout);
    });
</script>
<style>
    .matrix-container {
        margin-top: 33px;
    }
    .inner-container {
        max-width: 400px;
        max-height:450px;
    }
</style>
<div class="matrix-container">
    <div class="overflow-auto  flex  inner-container" on:scroll={onScroll}>
        {#each scores as score}
            <ScoreMatrixColumn name={score.name} scores={score.scores} type={score.type} />
        {/each}
    </div>
</div>
