<script>
  import { getContext, onDestroy } from 'svelte';
  import TeamStanding from "../TeamStanding/TeamStanding.svelte";

  export let standings;
  export let group = '';
  export let height = 450;

  const state = getContext('state');
  let debounceTimeout;
  let debounce = false;

  const onScroll = () => {
    if (!debounce) {
      if ($state && $state.analyticsUid) ga('send', 'event', 'Scroll', `${$state.analyticsUid}:${new Date().getTime()}`, 'Round Robin Group Standings');
      debounce = true;

      debounceTimeout = setTimeout(() => debounce = false, 1000);
    }
  };

  onDestroy(() => {
    clearTimeout(debounceTimeout);
  });
</script>

<div style="max-height: {height}px">
  <div class="bg-neutral-6  flex  h-10  uppercase  items-center  text-white  text-xs  mob:text-2xs" style="padding-right: 5px;">
      <div class="bg-neutral-8  w-10  flex items-center justify-center  h-full">
          Rank
      </div>
      <div class="pt-0.75  pb-0.75  flex  w-2/5  ml-2  items-center  justify-center">{group}</div>
      <div
          class="group-heading_headers  flex justify-end  flex-grow  h-full  normal-case"
      >
          <div class="flex  items-center  justify-center  uppercase  w-16  bg-neutral-8  text-center">
              Series Score
          </div>
          <div class="flex  items-center  justify-center  uppercase  w-16  bg-neutral-6  text-center">
              Game Score
          </div>
      </div>
  </div>

  <div class="overflow-y-scroll" style="max-height: {height - 37}px;" on:scroll={onScroll}>
  {#each standings as match}
      <TeamStanding standing={match} />
  {/each}
  </div>
</div>
