<style global lang="postcss">
  /* purgecss start ignore */
  @tailwind base;
  @tailwind components;
  /* purgecss end ignore */

  @tailwind utilities;
</style>
<script>
  import { setContext } from 'svelte';
  import { writable } from 'svelte/store';
  import { v4 as uuidv4 } from 'uuid';

  import initiateScheduleState, { scheduleStateQueue } from './services/ScheduleState/scheduleState';

  import { setupI18n, _, isLocaleLoaded } from '@weavr-esl/ui-core';
  import HorizontalNavigation from '@weavr-esl/ui-core/src/HorizontalNavigation/HorizontalNavigation.svelte';
  import TimingAnalytics from './TimingAnalytics/TimingAnalytics.svelte';
  import ScheduleMobile from './Schedule/ScheduleMobile.svelte';
  import StandingsMobile from './Standings/StandingsMobile.svelte';

  const getAnalyticsUid = () => {
    if (!localStorage.extensionAnalyticsUid) {
      const analyticsUid = uuidv4();
      localStorage.setItem('extensionAnalyticsUid', analyticsUid);

      return analyticsUid;
    }

    return localStorage.extensionAnalyticsUid;
  };

  let showExtension = true;
  let isLoaded = false;

  // Local app state
  let state = writable({ activeNavItem : {}, analyticsUid: getAnalyticsUid() });
  setContext('state', state);

  initiateScheduleState();

  const closeWidget = () => {
    ga('send', 'event', 'CloseWidget', `${$state.analyticsUid}:${new Date().getTime()}`, $state.activeNavItem.key);

    $state = { ...$state, activeNavItem: {} };
  }

  ga('send', 'event', 'User', 'Extension Loaded');

  // Twitch extension connectviity
  const twitch = window.Twitch ? window.Twitch.ext : null;

  $ : if (twitch && !isLoaded) {
    isLoaded = true;
    twitch.onContext((context, delta) => {
      if (delta.includes('arePlayerControlsVisible')) {
        if (!showExtension || !$state.activeNavItem.key) {
          showExtension = context.arePlayerControlsVisible;
        }
       }
    });

    twitch.listen('global', (target, contentType, body) => {
      //const bodyData = JSON.parse(body);
    });
  }

  $: if (!$isLocaleLoaded) {
  	setupI18n({ withLocale: 'en' });
  }

  const navigation = [
    { key: 'schedule', text: 'Schedule', widget: ScheduleMobile },
    { key: 'standings', text: 'Standings', widget: StandingsMobile },
  ];

  let activeNavItem = navigation[0];

  const navOnClick = (index) => activeNavItem = navigation[index];
</script>

<div>
  <TimingAnalytics />

  <HorizontalNavigation
    navigationItems={navigation.map((n, i) => ({ label: n.text, value: i, active: activeNavItem.key === n.key }))}
    onClick={navOnClick}
  />
  {#if $isLocaleLoaded}
    <svelte:component this={activeNavItem.widget} />
  {/if}
</div>
