<style>
    .team-standing:nth-child(2n + 1) {
        @apply bg-neutral-9;
    }

    .team-standing:nth-child(2n) {
        @apply bg-neutral-8;
    }
    .team-standing_position.positive {
        @apply border-secondary-1;
    }
    .team-standing_position.negative {
        @apply border-secondary-2;
    }
    .team-standing {
        height: 40px;
    }
</style>
<script>
    import { getContext } from 'svelte';
    import TeamLabel from "./../TeamLabel/TeamLabel.svelte";
    import TeamStandingResult from "./TeamStandingResult/TeamStandingResult.svelte"
    export let standing;

    let isExpanded = false;
    const state = getContext('state');

    const toggleExpandedView = () => {
      if ($state && $state.analyticsUid) {
        ga(
              "send",
              "event",
              "Tab Open",
              `${$state.analyticsUid}:${new Date().getTime()}`,
              `standings:roundRobinExpanded:${!isExpanded}:${standing.team.name}`
          );
      }

      isExpanded = !isExpanded;
    };
</script>

<div
    class="team-standing  flex border-t  text-white  uppercase  cursor-pointer  border-solid  border-neutral-6  text-xs  mob:text-2xs"
    on:click={toggleExpandedView}
>
    <div
        class={`team-standing_position  pt-0.75  pb-0.75  flex  w-10  justify-center border-r border-solid border-white  bg-black  bg-opacity-50  items-center ${standing.position_type}`}
    >
        {standing.position}
    </div>
    <div
        class="pt-0.75  pb-0.75  flex  w-2/5  ml-2  items-center"
    >
        <TeamLabel teamName={standing.team && standing.team.name || null} width={40} height={40} />
    </div>
    <div
        class="mr-10px  pt-0.75  pb-0.75  ml-auto  items-center"
    />
    <div
        class="p-0  w-32  flex  justify-end  text-right padding-0  items-center"
    >
        <span
            class="text-center  w-16  flex  h-full  items-center  justify-center  bg-black  bg-opacity-25"
        >
            {standing.series.win}
            {" - "}
            {standing.series.loss}
        </span>
        <span
            class="text-center  w-16  flex  h-full  items-center  justify-center  mr-0  bg-black  bg-opacity-50"
        >
            {standing.games.win}
            {" - "}
            {standing.games.loss}
        </span>
    </div>
</div>
{#if isExpanded}
    <div class="py-3  bg-black  bg-opacity-20">
        {#each standing.matches as match}
            <TeamStandingResult {match} />
        {/each}
    </div>
{/if}
