<script>
    import { getContext } from "svelte";
    import SwissStandings from "@weavr-esl/ui-core/src/SwissStandings/SwissStandings.svelte";
    import RobinStandings from "@weavr-esl/ui-core/src/RobinStandings/RobinStandings.svelte";
    import HorizontalNavigation from "@weavr-esl/ui-core/src/HorizontalNavigation/HorizontalNavigation.svelte";
    import TournamentLadder from "@weavr-esl/ui-core/src/TournamentBracket/TournamentBracket.svelte";
    import BracketExample from "@weavr-esl/ui-core/src/TournamentBracket/BracketExample/BracketExample.svelte";
    import ScoreMatrix from "@weavr-esl/ui-core/src/ScoreMatrix/ScoreMatrix.svelte";
    import { scheduleParser, getTeam } from "@weavr-esl/ui-core";

    export let standings = {};

    const state = getContext("state");

    const standingsMenu = ["GROUPS", "FINALS"];
    let activeMenu = "GROUPS";

    let availableGroups = standings?.tournament.stages?.[0].groups.map(
        (group) => group.id
    );
    let activeGroup = availableGroups[0] || 1;

    let grandFinal = null;

    let groupStandings =
        standings?.tournament.stages?.[0].groups?.[0].series || [];

    let isExpanded = false;

    let showBracket = false;

    const setShowBracket = (toggleType) => {
        showBracket = toggleType;
    };

    const setExpanded = (toggleType) => {
        if ($state && $state.analyticsUid) {
            ga(
                "send",
                "event",
                "Tab Open",
                `${$state.analyticsUid}:${new Date().getTime()}`,
                `standings:${!isExpanded ? "Show more" : "Show less"}`
            );
        }
        isExpanded = toggleType;
    };
    const setColumn = (match, depth, force = false) => {
        if (!match) return;
        if (match.depth === undefined || force) {
            match.depth = depth;
            setColumn(match.parents[0], depth - 1);
            setColumn(match.parents[1], depth - 1);
        }
        if (match.depth > depth + 1) {
            setColumn(match, depth + 1, true);
        }
    };

    const getMatchParser = (matches) => (match) => {
        const { teams, best_of_x, title, id, scheduled_start } = match;

        const parentsId = matches
            .filter(
                (parentMatch) =>
                    match.id === parentMatch.winner_advances_to ||
                    match.id === parentMatch.loser_advances_to
            )
            .map((parentMatch) => parentMatch.id);

        return {
            id,
            teams: teams.map((team) => ({
                ...team,
                winner: team.score === best_of_x,
            })),
            label: title,
            best_of_x,
            scheduled_start,
            parents: parentsId,
        };
    };

    const getBracket = () => {
        const bracketMatches = standings.tournament.stages.find(
            (stage) => "bracket_tree" in stage
        );
        if (!bracketMatches) return;

        const matches = bracketMatches.bracket_tree.map((match) => ({
            ...match,
            teams: [
                {
                    ...getTeam(match.participant_a, standings),
                    score: match.a_wins,
                },
                {
                    ...getTeam(match.participant_b, standings),
                    score: match.b_wins,
                },
            ],
        }));

        if (!matches || !matches.length) {
            return null;
        }
        const matchParser = getMatchParser(matches);

        const parsedMatches = matches.map(matchParser);

        // Map ids of parents to objects, so we can have recursive structure
        for (let i = 0; i < parsedMatches.length; i += 1) {
            for (let j = 0; j < parsedMatches[i].parents.length; j += 1) {
                parsedMatches[i].parents[j] = parsedMatches.find(
                    (match) => match.id === parsedMatches[i].parents[j]
                );
            }
        }

        setColumn(
            parsedMatches[parsedMatches.length - 1],
            parsedMatches.length
        );

        grandFinal = parsedMatches[parsedMatches.length - 1];
    };

    const setStandingsView = (value) => {
        if ($state && $state.analyticsUid) {
            ga(
                "send",
                "event",
                "Tab Open",
                `${$state.analyticsUid}:${new Date().getTime()}`,
                `standings:${value}`
            );
        }

        setExpanded(false);
        setShowBracket(value === "FINALS");

        activeMenu = value;
        getBracket();
    };

    const setActiveGroup = (groupID) => {
        if ($state && $state.analyticsUid) {
            ga(
                "send",
                "event",
                "Tab Open",
                `${$state.analyticsUid}:${new Date().getTime()}`,
                `standings:changeGroupTo:${groupID}`
            );
        }

        activeGroup = groupID;

        groupStandings =
            standings?.tournament.stages?.[0].groups.find(
                (group) => group.id === groupID
            )?.series || [];
    };

    const getGroupLabel = (groupID, standings) => {
        const activeGroupLabel = standings?.tournament.stages?.[0].groups.find(
            (group) => group.id === groupID
        )?.division;

        return activeGroupLabel || ''
    };

    const getScoresForTeam = (scheduleData, rosterId) =>
        scheduleData.map((teamEntry) => {
            const opponentId = teamEntry.team.rosterId;
            if (opponentId === rosterId) return null;

            const matches = teamEntry.matches.filter(
                (match) =>
                    match.team_one.rosterId === rosterId ||
                    match.team_two.rosterId === rosterId
            );

            const teamScore = matches
                .map((match) =>
                    match.team_one.rosterId === rosterId
                        ? match.team_one_score
                        : match.team_two_score
                )
                .map((score) => Number(score) || 0)
                .reduce((a, b) => a + b, 0);
            const opponentScore = matches
                .map((match) =>
                    match.team_one.rosterId !== rosterId
                        ? match.team_one_score
                        : match.team_two_score
                )
                .map((score) => Number(score) || 0)
                .reduce((a, b) => a + b, 0);

            return `${opponentScore}:${teamScore}`;
        });

    const getMatrixData = (standings, activeGroup) => {
        const scheduleData = scheduleParser(standings, activeGroup);

        const matrixData = scheduleData.map((teamEntry) => ({
            name: teamEntry.team.name,
            scores: getScoresForTeam(scheduleData, teamEntry.team.rosterId),
            type: "score",
        }));

        matrixData.unshift({
            name: null,
            scores: scheduleData.map((teamEntry) => teamEntry.team.name),
            type: "logo",
        });

        return matrixData;
    };
</script>

<div
    class="bg-black  bg-opacity-75  overflow-hidden  match-standings-container  relative"
    style={`width: ${
        isExpanded || showBracket ? "1000" : "501"
    }px; height: 576px; backdrop-filter: blur(3px);`}
>
    <div class="flex  overflow-hidden" style="max-height: 500px;">
        <div class="mx-8  my-4  flex-1">
            {#if standings.tournament.stages.length > 1}
                <div class="text-xs  mb-2">
                    <HorizontalNavigation
                        navigationItems={standingsMenu.map((menu) => ({
                            label: menu,
                            value: menu,
                            active: activeMenu === menu,
                        }))}
                        onClick={setStandingsView}
                        align="left"
                    />
                </div>
            {/if}
            {#if availableGroups.length > 1 && activeMenu === "GROUPS"}
                <div class="text-xs  mb-2">
                    <HorizontalNavigation
                        navigationItems={availableGroups.map((groupID) => ({
                            label: getGroupLabel(groupID, standings),
                            value: groupID,
                            active: groupID === activeGroup,
                        }))}
                        onClick={setActiveGroup}
                        align="left"
                    />
                </div>
            {/if}
            {#if activeMenu === "GROUPS"}
                {#if standings.tournament.stages[0].format === "swiss"}
                    <SwissStandings standings={groupStandings} />
                {:else}
                    <RobinStandings
                        standings={scheduleParser(standings, activeGroup)}
                        group={getGroupLabel(activeGroup, standings)}
                    />
                {/if}
            {:else if grandFinal !== null}
                <TournamentLadder
                    match={grandFinal}
                    depth={grandFinal.depth}
                    bracket={BracketExample}
                />
            {/if}
        </div>
        {#if isExpanded}
            <div class="mx-8  my-4  flex-1">
                <ScoreMatrix scores={getMatrixData(standings, activeGroup)} />
            </div>
        {/if}
    </div>
    {#if !showBracket}
        <div
            class="absolute  left-0  w-full  bottom-0  items-center  flex  justify-center  bg-black  text-white  h-10  cursor-pointer"
            on:click={() => setExpanded(!isExpanded)}
        >
            {!isExpanded ? "Show more" : "Show less"}
        </div>
    {/if}
</div>

<style>
    :global(.match-standings-container > .ladder-container) {
        position: relative;
        top: 15px;
    }
</style>
