<script context="module">
  import moment from 'moment';

  const getLabelProps = (match) => {
    if (match.is_live === true) {
      return {
        color: 'secondary-2',
        text: 'Live'
      };
    }

    if (moment().isAfter(moment(match.scheduled_start)) && match.team_one_score === 0 && match.team_two_score === 0) {
      return {
        color: 'gray-300',
        text: 'Soon',
      }
    }

    if (moment().isAfter(moment(match.scheduled_start))) {
      return {
        color: 'primary-2',
        text: 'Result',
      }
    }

    if (moment().isBefore(moment(match.scheduled_start))) {
      return {
        color: 'gray-300',
        text: moment(match.scheduled_start).format('HH:mm')
      }
    }


    return { type: '', text: '' };
  }
</script>
<script>
  import BorderLabel from '../BorderLabel/BorderLabel.svelte';
  import TeamLabel from '../TeamLabel/TeamLabel.svelte';

  export let match = {
    round_name: '',
    team_one: '',
    team_one_score: 0,
    team_two: '',
    team_two_score: 0,
    scheduled_start: '2020-01-01T00:00:00.000Z',
    is_live: false,
  };

  let labelProps = getLabelProps(match);
  $ : labelProps = getLabelProps(match);
</script>

<div class="bg-primary-1  text-white  rounded  items-center  flex  p-4  text-xs  mob:text-2xs">
  <BorderLabel color={labelProps.color}>
    {labelProps.text}
  </BorderLabel>

  <div class="flex-1  flex">
    <div class="flex  flex-1  items-center  justify-center  px-1">
      <TeamLabel textPosition="left" textAlign="text-right" teamName={match.team_one} />
    </div>

    <div class="flex  flex-col">
      <div class="flex  justify-center">
        <div class="bg-gray-800  p-2  rounded-sm  mr-2">{match.team_one_score}</div>

        <div class="bg-gray-800  p-2  rounded-sm  ml-2">{match.team_two_score}</div>
      </div>
      <div class="text-center  text-2xs">{match.round_name}</div>
    </div>

    <div class="flex  flex-1  items-center  justify-center  px-1">
      <TeamLabel teamName={match.team_two} />
    </div>
  </div>
</div>
