<script>
  import { tooltip } from '../actions/tooltip.js';

  export let teamName = '';
  export let width = 50;
  export let height = 50;
  export let tooltipText = '';
  export let tooltipPosition = 'right';
  export let showTooltip = false;
  export let src = '';

  const props = {
    allowHTML: true,
    placement: tooltipPosition,
    content: tooltipText,
  };
</script>

{#if src || teamName}
  <img
    src={src || `images/teams/${teamName.replace(/_/g, '').replace(/ /g, '').replace(/\./g, '').toUpperCase()}.png`}
    width={width}
    height={height}
    alt=""
    use:tooltip={showTooltip ? props : null}
  />
{/if}
