<style>
    .swiss-standing-entry:nth-child(2n+1) {
        @apply bg-neutral-5;
    }
    .swiss-standing-entry:nth-child(2n) {
        @apply bg-neutral-1;
    }
    .swiss-standing {
        max-height: 510px;
    }
    .swiss-standing .swiss-standing-entry:last-child {
        border-top: none !important;
    }
    :global(.swiss-standing .swiss-standing-entry > .team-name img) {
        @apply mx-4px;
    }
    :global(.swiss-standing .swiss-standing-round img) {
        @apply h-8;
    }
    .swiss-standing .team-name {
        width: 136px;
    }
    :global(.swiss-standing img) {
        @apply h-10;
    }
</style>

<script>
    import TeamLabel from '../TeamLabel/TeamLabel.svelte';
    import TeamLogo from "../TeamLogo/TeamLogo.svelte";

    export let standings;

    const getRoundResultClass = (round) => {
        if (round.teamScore === null || round.opponentScore === null) return "";
        return round.teamScore > round.opponentScore
            ? "text-secondary-1"
            : "text-secondary-2";
    };


</script>
<div class="swiss-standing  overflow-auto  bg-neutral-1  text-xs">
    <div class="flex  h-9  sticky  top-0   bg-black  text-white">
        <div
            class="flex  items-center  justify-center  w-9  border-r  border-solid  border-transparent  text-12pt  bg-opacity-50  bg-black"
        >
            #
        </div>
        <div class="team-name  flex  items-center  justify-center">
            Team
        </div>
        <div
            class="swiss-standing-matches  flex  items-center  justify-center  w-16"
        >
            Matches
        </div>
        <div
            class="swiss-standing-round  flex  items-center  justify-center  w-14  flex-col"
        >
            Round 1
        </div>
        <div
            class="swiss-standing-round  flex  items-center  justify-center  w-14  flex-col"
        >
            Round 2
        </div>
        <div
            class="swiss-standing-round  flex  items-center  justify-center  w-14  flex-col"
        >
            Round 3
        </div>
        <div
            class="swiss-standing-round  flex  items-center  justify-center  w-14  flex-col"
        >
            Round 4
        </div>
        <div
            class="swiss-standing-round  flex  items-center  justify-center  w-14  flex-col"
        >
            Round 5
        </div>
    </div>
    <div class="swiss-standing-content">
        {#each standings as standing, i}
            <div class="swiss-standing-entry  flex  h-13  ">
                <div
                    class={`rank  flex  items-center   justify-center  w-9  border-r  border-solid  border-transparent  text-12pt  bg-opacity-50  bg-black    text-white  ${
                        i < standings.length / 2
                            ? "border-secondary-1"
                            : "border-secondary-2"
                    }`}
                >
                    {i + 1}
                </div>
                <div
                    class="team-name  flex  justify-start  items-center  text-white"
                >
                    <TeamLabel
                        teamName={standing.team}
                        width={"auto"}
                        height={25}
                    />
                </div>
                <div
                    class="swiss-standing-matches  flex  items-center  justify-center  w-16  text-white"
                >
                    {standing.rounds.filter(
                        (round) =>
                            round.teamScore !== null &&
                            round.opponentScore !== null &&
                            round.teamScore > round.opponentScore
                    ).length}
                    :
                    {standing.rounds.filter(
                        (round) =>
                            round.teamScore !== null &&
                            round.opponentScore !== null &&
                            round.opponentScore > round.teamScore
                    ).length}
                </div>
                <div
                    class="swiss-standing-rounds-container  flex-1  flex  items-center  justify-start  text-white"
                >
                    {#each Array(5) as _, i}
                        {#if standing.rounds[i]}
                            <div
                                class={`swiss-standing-round  flex  items-center  justify-center  flex-col  w-14  h-full  ${getRoundResultClass(
                                    standing.rounds[i]
                                )}`}
                            >
                                <TeamLogo
                                    teamName={standing.rounds[i].opponent.replace(/_/g, '')}
                                    width={"auto"}
                                    height={25}
                                />
                                {standing.rounds[i].teamScore !== null &&
                                standing.rounds[i].opponentScore !== null
                                    ? `${standing.rounds[i].teamScore}:${standing.rounds[i].opponentScore}`
                                    : null}
                            </div>
                        {:else}
                            <div
                                class="swiss-standing-round  flex  items-center  justify-center  flex-col  w-12  h-full"
                            />
                        {/if}
                    {/each}
                </div>
            </div>
        {/each}
    </div>
</div>
