<script>
    import { getContext } from 'svelte';
    import { tooltip } from '../../actions/tooltip.js';
    import LadderLine from './../LadderLine/LadderLine.svelte';

    export let teams = [];
    export let first = false;
    export let bracket;
    export let startTime = null;

    const days = ['Sun', 'Mon', 'Tues', 'Wed', 'Thur', 'Fri', 'Sat'];
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];
    const dateObj = new Date(startTime);

    const state = getContext('state');

    const props = {
      placement: 'top',
      content: `${days[dateObj.getDay()]} ${dateObj.getDate()} ${months[dateObj.getMonth()]} @ ${dateObj.getHours()}:${dateObj.getMinutes().toString().padStart(2, '0')}`,
    };

    const matchHover = () => {
      if ($state && $state.analyticsUid) {
        ga('send', 'event', 'Hover', `${$state.analyticsUid}:${new Date().getTime()}`, `bracketmatch:${teams[0].name}:${teams[1].name}:${new Date(startTime).getTime()}`);
      }
    };
</script>

<div
    class="flex  flex-col  relative  z-1  match-bracket  self-center  my-8px mx-6px  {!teams.length ? 'empty':''}"
    use:tooltip={startTime ? props : null}
    on:mouseenter={() => matchHover()}
>
    <LadderLine {first} />
    <svelte:component this={bracket} {teams} />
</div>
